*::-webkit-scrollbar{
    display: none;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    -ms-overflow-style: none; /* for Internet Explorer, Edge */
    scrollbar-width: none; /* for Firefox */
    
}

/* Font */

@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap');

@font-face{
    font-family: 'NINJA';
    src: local('NINJA'),
         url('./files/NINJA.woff') format('woff');
}

/* Animations */

@keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }


/* Components */

.main {
    display: grid;
    grid-template-areas: "top top top"
                        "mid mid mid";
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 63px auto 70px;
    background-color: #000;
}

.top{
    grid-area: top;
    width: 100%;
    height: 100%;
}


.fa-angle-double-down{
    padding-left: 5px;
}

.appBar{
    animation: 1s ease-out 0s 1 fadeIn;
}

.tBar{
    justify-content: space-around;
}


.divButtons{
    padding-right: 100;
}

.burger{
    display: none !important;
}

.fa-user-ninja{
    padding-left: 10px;
}

.modal{
    display: flex;
    flex-direction: column;
    color: #fff;
    position:absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 400;
    border-style: 2px solid #000;
    border-radius: 4;
    box-shadow: 24;
}

.overlay{
    display: inline-flex;
    position: absolute;
    width: 100%;
    height: 100%;
    align-items: center;
    
}

.buttons-hero{
    display: inline-flex;
    position: absolute;
    align-items: center;
}


.container1{
    align-items: center;
    justify-content: center;
    margin-right: 60px;
    padding-left: 60px;
    
}

.container2{
    align-items: center;
    justify-content: center;
    max-width: 40vw;
    padding-bottom: 50px;
}


.videoTag{
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    
}




/* Media Queries */

@media (max-width: 956px) {
    .button{
        display:none !important;
    }

    .burger{
        display:inline !important;
    }

    .bars{
        justify-content: right;
    }
    .tBar{
        justify-content: space-between;
    }
    .mid{
        display:none !important
    }

  }

  @media(max-width: 1360px){
      .container2{
          padding-left: 5%;
      }
  }

  @media(max-width: 1276px){
    .container2{
        padding-left: 1%;
    }

  }

  @media(max-width: 833px){
    * {
        box-sizing: border-box;
        margin: 0;
        padding: 0;
        -ms-overflow-style: none; /* for Internet Explorer, Edge */
        scrollbar-width: none; /* for Firefox */
        overflow-x: hidden;
        
    }
    .overlay{
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        height: 100%;
        background-image: url("../src/files/bg.png");
    }
    .container1{
        display: flex;
        align-items: center;
        align-content: center;
        justify-content: center;  
        width: 100%;
        padding-left: 0;
        margin-right: 0;
        margin-bottom: -8%;
    }
    .buttons-hero{
        display: flex;
        flex-wrap: wrap;
        
    }
    .container2{
        text-align: center;
        max-width: 50vw;   
    }
    .videoTag{
        display: none;
        
        }
    
    }

    @media(max-width: 515px){
        .fa-user-ninja{
            display: none;
        }
        .container2{
            align-items: center;
            justify-content: center;
            max-width: 89vw;
            
            
        }
        
    }


